import * as React from 'react'
import Layout from '../components/Layout'
import { NextPage } from 'next'
import AskRangeDialog from '../components/ask-range-prompt'
import AskExcludeDialog from '../components/ask-exclude-prompt'
import SlotMachine from '../components/slot-machine'
import { Button } from '@material-ui/core'

const IndexPage: NextPage = () => {
  const [lowerBound, setLowerBound] = React.useState(0)
  const [upperBound, setUpperBound] = React.useState(2000)
  const [excludeValues, setExcludeValues] = React.useState<number[]>([])

  const [currentText, setCurrentText] = React.useState('1234')
  const [turning, setTurning] = React.useState(false)

  const getValue = (lb: number, ub: number) => {
    setLowerBound(lb)
    setUpperBound(ub)
  }
  const excluding = (values: number[]) => {
    setExcludeValues(values)
  }

  const random = () => {
    const r = []
    for (var i = lowerBound; i <= upperBound; i++) {
      if (excludeValues.indexOf(i) === -1)
        r.push(i)
    }
    var d = new Date()
    const offset = d.getTime() % r.length
    const newValue = r[offset]
    setExcludeValues([...excludeValues, newValue])
    const t = (10000 + newValue) + ''
    setCurrentText(t.substr(1))


    setTurning(true)
  }

  return (
    <Layout title="Krungthai Axa Distribution Day">
      <AskRangeDialog selectValue={getValue}></AskRangeDialog>
      <AskExcludeDialog excluding={excluding}></AskExcludeDialog>
      <div className='cover-box'>
        <img style={{ width: '100%' }} src='/static/bg.jpg' />

        <div className='slot-box'>
          <SlotMachine text={currentText}></SlotMachine>
          <div style={{ padding: 10, textAlign: 'center' }}>

            <Button variant="contained" color="primary" onClick={random}>
              Random
</Button>
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default IndexPage
